.content-body {  
    &.wish-content{  
        padding: 54px 0 60px;
        .subMenu-container {
            margin: 10px 0 5px;
            padding: 0 15px;
            .sub-txt {
                @include fontSize(12px,400);
                line-height: 18px;
                color: #666;
                margin-bottom: 20px;
            }
            .total-info {
                position: relative;
                &:after {
                    @include clearBoth;
                }
                p {
                    @include fontSize(14px,400);
                    color: #333;
                    line-height: 20px;
                    float: left;
                    position: relative;
                    span {
                        color: #ee5322;
                        @include fontSize(14px,700);
                    }                
                    .per {
                        @include fontSize(12px,400);
                        color: #000;
                        position: absolute;
                        top: 50%;                    
                        transform: translate(0,-50%);      
                    }
                }
                .clear {
                    color: #333;
                    @include fontSize(11px,300);
                    line-height: 17px;
                    background: none;
                    border: 0;
                    right: 0;
                    height: 17px;
                    position: absolute;
                    top: 50%;                    
                    transform: translate(0,-50%);                    
                }
            }
        }
        .wishList-container {    
            padding: 0 15px;  
            .wish-wrapper {
                @include flexCenter;
                padding: 20px 0;
                border-bottom: 1px solid #eeeeee;
                &:first-child {
                    border-top: 1px solid #eee;
                }
                .img-box {
                    background: url(../img/image_09.png) center center;
                    width: 80px;
                    height: 80px;
                    margin-right: 10px;
                }
                .word-box {
                    height: 80px;
                    width: calc( 100% - 90px);
                    position: relative;   
                    &.cancle {
                        opacity: 0.5;
                    }
                    .toTop {      
                        margin-bottom: 10px; 
                        position: relative;                    
                        &:after {
                            @include clearBoth;
                        }
                        p {
                            float: left;
                            margin-top: 2px;
                            span {
                                color: #fff;
                                @include fontSize(12px,300);
                                line-height: 18px;
                                background: #000;
                                padding: 3px 9px;
                                border-radius: 12px;
                                margin-right: 5px;
                            }
                        }
                        button {
                            border: 0;
                            background: none;
                            position: absolute;
                            right: 0;
                            height: 10px;
                            width: 10px;
                            @include flexAllCenter;
                        }
                    }
                    .price { 
                        position: absolute;
                        width: 100%;
                        bottom: -2px;
                        .priceNum {
                            float: right;
                            color: #333;
                            @include fontSize(12px,400);
                            span {
                                color: #333;
                                @include fontSize(15px,700);
                                line-height: 18px;
                            }
                        }
                    }
                }
            }
        }
    }
}

