.content-body {  
    &.pickup-content{  
        padding: 54px 0 60px;
        .subMenu-container {
            margin: 10px 0 5px;
            padding: 0 15px;
            &:after {
                @include clearBoth;
            }
            .select-box {
                float: right;
                position: relative;
                padding-right: 17px;
                &:after {
                    width: 10px;
                    height: 8px;
                    content: "";
                    position: absolute;
                    background: url(../img/icon_select01.png) center center no-repeat;
                    right: 0;           
                    top: 50%;                    
                    transform: translate(0,-50%);
                }
                .listUp-btn {
                    border: 0;
                    height: 18px;
                    color: $blackColor;
                    @include fontSize(12px,500);
                }
            }
        }
        .pickupList-container {    
            padding: 0 15px;  
            .pickup-wrapper {
                @include flexCenter;
                padding: 20px 0;
                border-bottom: 1px solid #eeeeee;
                &:first-child {
                    border-top: 1px solid #eee;
                }
                &:last-child{
                    border-bottom: 0;            
                }  
                .img-box {
                    background: url(../img/image_09.png) center center;
                    width: 80px;
                    height: 80px;
                    margin-right: 10px;
                }
                .word-box {
                    height: 80px;
                    width: calc( 100% - 90px);
                    position: relative;   
                    &.cancle {
                        opacity: 0.5;
                    }
                    .date {      
                        margin-bottom: 7px;        
                        &:after {
                            @include clearBoth;
                        }
                        p {
                            font-size: 12px;
                            color: #333333;
                            float: left;
                            line-height: 14px;
                        }
                        button {
                            border: 0;
                            background: none;
                            float: right;
                            height: 7px;
                            top: 50%;                    
                            transform: translate(0,-50%);
                        }
                    }
                    .menuInfo {
                        p {
                            color: #333;
                            @include fontSize(14px,500);
                            line-height: 20px;
                            @include flexCenter;
                            @include wordClear;
                            width: 100%;
                            span {
                                color: #fff;
                                @include fontSize(12px,300);
                                line-height: 18px;
                                background: #000;
                                padding: 3px 9px;
                                border-radius: 12px;
                                margin-right: 5px;
                                display: inline-block;
                            }
                        }
                    }
                    .price { 
                        position: absolute;
                        width: 100%;
                        bottom: -2px;
                        &:after {
                            @include clearBoth;
                        }                   
                        .select-status {
                            float: left;
                            span {
                                color: $mainColor;
                                @include fontSize(12px,500);
                                line-height: 18px;
                            }
                        }
                        .priceNum {
                            float: right;
                            color: #333;
                            @include fontSize(12px,400);
                            span {
                                color: #333;
                                @include fontSize(15px,700);
                                line-height: 18px;
                            }
                        }
                    }
                }
            }
        }
        .moreBtn-container {
            border-top: 1px solid #eee;
            button {
                border: 0;
                background: none;
                height: 40px; 
                width: 100%; 
                color: #000;
                @include fontSize(12px,400);
                line-height: 23px;
                img {
                    margin-left: 5px;
                    height: 8px;
                    width: 10px;
                    top: 50%;    
                }
            }
        }
    }
}

