.content-body {
    &.orderDetail {
        padding: 54px 0 0;        
        .content-inner {
            height: 100vh;
            .textSection {
                @include fontSize(14px,700);
                line-height: 23px;
                padding: 10px 15px 0;
            }            
            .titleSection {                
                border-bottom: 1px solid #eeeeee;    
                padding: 10px 15px 25px;
                &:after {
                    @include clearBoth;
                }
                &:last-child{
                    border-bottom: 0;            
                }        
                .img-area {
                    background: url(../img/image_09.png) center center;
                    width: 80px;
                    height: 80px;
                    margin-right: 10px;
                    float: left;    
                }
                .word-area {
                    width: calc( 100% - 90px);
                    float: right;
                    &.cancle {
                        opacity: 0.5;
                    }
                    .top-box {      
                        margin-bottom: 10px; 
                        position: relative;                    
                        &:after {
                            @include clearBoth;
                        }
                        p {
                            @include fontSize(11px,300);
                            color: #333333;
                            float: left;
                            line-height: 14px;
                            margin-top: 2px;
                            span {
                                color: #fff;
                                @include fontSize(12px,300);
                                line-height: 18px;
                                background: #000;
                                padding: 3px 9px;
                                border-radius: 12px;
                                margin-right: 5px;
                            }
                        }
                        button {
                            &.complete {
                                border: 1px solid $mainColor;
                                p {
                                    color: $mainColor;
                                }
                            }
                            border: 1px solid rgba(#000, 0.2);
                            background: #fff;
                            position: absolute;
                            right: 0;
                            height: 28px;
                            width: 60px;
                            @include flexAllCenter;
                        }
                    }
                    .menu-box {
                        margin-bottom: 11px;
                        p {
                            color: #333;
                            @include fontSize(14px,500);
                            line-height: 22px;
                            @include flexCenter;
                            width: calc( 100% - 5px);
                        }
                    }
                    .bottom-box {
                        width: 100%;
                        &:after {
                            @include clearBoth;
                        }
                        .goodsStatusInfo {
                            float: left;
                            &:after {
                                @include clearBoth;
                            }    
                            .paymentComplete {
                                float: left;
                                @include fontSize(12px,500);
                                line-height: 18px;
                                color: rgba($mainColor, .4);
                                margin-right: 5px;
                                text-decoration: line-through;
                            }
                            .paymentCancle {
                                float: right;
                                @include fontSize(12px,500);
                                line-height: 18px;
                                color: #e52222;

                            }
                        }
                        .priceInfo { 
                            float: right;
                            &:after {
                                @include clearBoth;
                            }                   
                            .priceNum {
                                float: left;
                                color: #333;
                                @include fontSize(12px,400);
                                margin-right: 5px;
                                line-height: 18px;
                                span {
                                    color: #333;
                                    @include fontSize(15px,700);
                                    line-height: 18px;
                                }
                            }
                            .pricePer {
                                float: right;
                                color: #333;
                                @include fontSize(12px,400);
                                line-height: 18px;
                                span {
                                    color: #333;
                                    @include fontSize(15px,700);
                                    line-height: 18px;
                                }
                            }
                        }
                    }
                }
            }
            .barSection {
                height: 7px;
                width: calc( 100% + 2px );
                background: #f6f6f6;
                border: 1px solid #dcdcdc;
            }
            .mainSection {
                .content-area {
                    padding: 25px 15px;
                    &.lastMenu {
                        padding-bottom: 30px;
                    }
                    .info-box {
                        .detailItem {
                            margin-bottom: 5px;     
                            &:last-of-type {
                                margin-bottom: 0;
                            }
                            &:after {
                                @include clearBoth;
                            }
                            &.plusLine {
                                padding-bottom: 10px;
                                margin-bottom: 10px;
                                border-bottom: 1px solid #eeeeee;    
                            }
                            .paragraphTitle {
                                @include fontSize(14px,700);
                                line-height: 20px;
                                color: #000;
                                margin-bottom: 10px;        
                            }
                            .tableTitle {        
                                @include fontSize(14px,400);
                                color: #333;
                                line-height: 20px;
                                float: left;
                                width: 94px;
                                margin-right: 60px;
                            }
                            .goodsContent {
                                @include fontSize(14px,500);
                                color: #000;
                                line-height: 20px;
                                float: right;
                                width: calc( 100% - 154px );
                                text-align: right;
                                &.colorText {
                                    @include fontSize(12px,400);
                                    line-height: 18px;
                                    color: $mainColor;
                                }
                                span {
                                    &.boldPrice {
                                        @include fontSize(14px,700);                               
                                    }
                                    &.mediumPrice {
                                        @include fontSize(14px,500);                               
                                    }
                                    &.colorPrice {
                                        @include fontSize(23px,700);
                                        color: $mainColor;
                                        line-height: 28px;
                                    }
                                    @include fontSize(14px,400);
                                    line-height: 17px;                                    
                                }
                            }  
                        }
                    }
                }
            }
            .btn-area {
                .ok {
                    border: 0;                            
                    height: 60px;
                    width: 100%;
                    background: $mainColor;
                    color: #ffffff;
                    @include fontSize(14px,500);                          
                }
            }
        }
    }
}









