.index-box {
    .popup-wrapper {       
        display: none;
        background: rgba(0,0,0,.5);
        position: fixed;
        width: 100%;
        height: 100vh;
        bottom: 0;
        &.active {
            @include flexCenter;
        }
        &.shoppingBasket {
            padding: 0 30px;
            .popup-inner {                
                background: #ffffff;
                width: 100%;                
                .content-wrap {
                    .text-box {
                        position: relative;
                        .close {
                            position: absolute;
                            top: 15px;
                            right: 15px;
                            height: 10px;
                            width: 10px;
                            @include flexCenter;
                        }
                        p {
                            color: #000;
                            @include fontSize(14px,400);
                            text-align: center;
                            padding: 40px 0;                          
                        }
                    }
                    .btn-box {
                        &.one-btn {
                            .go {
                                width: 100%;
                            }
                        }
                        &:after {
                            @include clearBoth;
                        }
                        .continue {
                            background: none;
                            border: 0;
                            color: #ffffff;
                            @include fontSize(14px,400);
                            background: #000;
                            height: 45px;
                            width: 50%;
                            float: left;                            
                        }
                        .go {
                            background: none;
                            border: 0;
                            color: #ffffff;
                            @include fontSize(14px,400);
                            background: $mainColor;
                            height: 45px;
                            width: 50%;
                            float: right;
                        }
                    }
                }
            }            
        }
        &.photoReview {
            padding: 0 30px;
            .popup-inner {
                background: #ffffff;
                width: 100%;
                position: relative;
                .close {
                    position: absolute;
                    top: 15px;
                    right: 15px;
                    height: 10px;
                    width: 10px;
                    @include flexCenter;
                }
                .content-wrap {
                    padding: 30px 30px 40px;
                    .text-box {
                        position: relative;
                        p {
                            @include fontSize(17px,700);
                            line-height: 25px;
                            padding-bottom: 20px;
                            text-align: center;
                        }
                    }
                    .star-box {
                        width: 100%;                        
                        margin-bottom: 10px;
                        &:after {
                            @include clearBoth;
                        }
                        .starImg {
                            float: left;
                            img {
                                width: 19px;
                                height: 18px;
                                float: left;
                                margin-right: 1px;
                            }
                        }
                        .nickname {
                            float: right;
                            p {
                                @include fontSize(10px,300);
                                span {
                                    @include fontSize(12px,700);
                                    line-height: 20px;
                                }
                            }
                        }                     
                    }
                    .content-box {          
                        img {
                            width: 100%;
                            margin-bottom: 15px;
                            max-height: 330px;
                            overflow: scroll;
                        }
                        p {
                            @include fontSize(12px,500);
                            line-height: 20px;
                            color: #333;
                        }
                    }
                }
            }
        }
        &.completePay {
            padding: 0 30px;            
            .popup-inner {
                background: #ffffff;
                width: 100%;             
                .titleSection {
                    position: relative;             
                    padding: 30px 15px 30px;
                    .close {
                        position: absolute;
                        height: 10px;
                        width: 10px;
                        right: 15px;
                        top: 20px;
                        @include flexCenter;
                    }
                    .text-box {
                        p {
                            @include fontSize(15px,700);
                            color: #000;
                            text-align: center;
                        }
                    }
                }
                .mainSection {
                    max-height: calc( 100vh - 192px );
                    overflow-y: scroll;
                    .content-area {
                        padding: 0 15px 25px;
                        .info-box {
                            border-bottom: 1px solid #eeeeee;
                            padding: 15px 0 10px;
                            &:first-of-type {
                                padding-top: 0;    
                            }
                            &:last-of-type {
                                border-bottom: 0;
                                padding-bottom: 0;
                            }
                        }
                        .detailItem {
                            margin-bottom: 5px;                                
                            &:after {
                                @include clearBoth;
                            }
                            .dashedImg {
                                background: url(../img/dashed.png) center center repeat;
                                height: 1px;
                            }
                        }
                        .tableTitle {
                            @include fontSize(12px,400);
                            color: #333;
                            line-height: 18px;
                            float: left;
                            width: 67px;
                        }
                        .goodsContent {
                            &.total {
                                @include fontSize(12px,500);     
                                span {
                                    @include fontSize(14px,700);
                                    line-height: 17px;                                    
                                }
                            }
                            &.num {
                                @include fontSize(14px,700);
                                color: $mainColor;
                                line-height: 17px;
                            }
                            &.device {
                                @include fontSize(12px,500);
                                color: $mainColor;                            
                            }
                            @include fontSize(12px,500);
                            color: #000;
                            line-height: 18px;
                            float: right;
                            width: calc( 100% - 107px );
                            text-align: right;
                            span {
                                @include fontSize(12px,500);
                                line-height: 14px;                                    
                            }
                        }    
                    }
                    .notice-area {
                        text-align: center;
                        padding: 0 15px 40px;
                        .orderNum {
                            @include fontSize(17px,700);
                            color: #000;
                            line-height: 25px;
                        }
                        .subText {
                            @include fontSize(12px,300);
                            color: #000;
                            line-height: 18px;
                        }
                    }
                    .btn-area {
                        .ok {
                            border: 0;                            
                            height: 40px;
                            width: 100%;
                            background: $mainColor;
                            color: #ffffff;
                            @include fontSize(14px,500);                          
                        }
                    }
                }
            }            
        }
        &.access {
            padding: 0 30px;
            .popup-inner {
                background: #ffffff;
                width: 100%;                
                .content-wrap {                    
                    padding: 30px 15px 30px;
                    position: relative;
                        .close {
                            position: absolute;
                            top: 15px;
                            right: 15px;
                            width: 10px;
                            height: 10px;
                            @include flexCenter;
                        }
                    .text-box {
                        margin-bottom: 20px;                        
                        p {
                            @include fontSize(18px,700);
                            color: #000;
                            line-height: 26px;
                        }
                    }
                    .content-box { 
                        .allowInfo {
                            padding: 10px 5px 10px;
                            border-bottom: 1px solid #eeeeee;
                            &:last-of-type {
                                border: 0;
                            }
                            &:after {
                                @include clearBoth;
                            }
                            img {
                                float: left;
                                margin-right: 10px;
                            }
                            .text-area {
                                float: left;
                                .main-text {
                                    @include fontSize(14px,700);
                                    color: #000;
                                    line-height: 18px;
                                    margin-bottom: 4px;
                                }
                                .sub-text {
                                    @include fontSize(12px,400);
                                    color: #000;
                                    line-height: 18px;
                                }
                            }
                        }                
                    }    
                }
                .btn-box {
                    .ok {
                        border: none;
                        background: $mainColor;
                        width: 100%;
                        height: 40px;
                        @include fontSize(14px,500);
                        color: #ffffff;                        
                    }                    
                }                
            }
        }
        &.qaSelect {
            .popup-inner {
                background: #ffffff;
                width: 100%;
                position: fixed;
                bottom: 0;
                .content-wrap {
                    position: relative;
                    .close {
                        position: absolute;
                        right: 15px;
                        top: -25px;
                        @include flexCenter;
                        height: 10px;
                        width: 10px;
                    }
                    .menu-container {
                        .questionMessage {
                            padding: 0 20px;
                            height: 30px;
                            @include flexCenter;
                            border-bottom: 1px solid #edf0f3;
                            p {
                                color: #999999;
                                @include fontSize(11px,300);
                            }
                        }
                        .selectBox {
                            .questionBtn {
                                &.contact {
                                    @include fontSize(12px,700);
                                    color: #000;     
                                    background: #f5f5f5;                         
                                }        
                                padding: 0 20px;
                                height: 48px;
                                width: 100%;
                                text-align: left;                     
                                border: 0;
                                background: none;
                                @include fontSize(12px,400);
                                line-height: 18px;          
                                border-bottom: 1px solid #edf0f3;         
                            }
                        }
                    }
                }
            }
        }

        &.shoppingOption {
            .popup-inner {
                width: 100%;
                background: #ffffff;        
                position: fixed;
                bottom: 0;        
                .content-wrap {
                    position: relative;
                    .close {
                        position: absolute;
                        right: 15px;
                        top: 23px;
                        @include flexCenter;
                        height: 10px;
                        width: 10px;
                    }
                    .menu-container {                        
                        .menuTitle {
                            padding: 15px;
                            
                            border-bottom: 1px solid #eeeeee;
                            p {
                                color: #000;
                                @include fontSize(17px,500);
                                line-height: 25px;
                                width: calc( 100% - 55px );
                            }
                        }
                    }
                    .optionBox {
                        &:after {
                            @include clearBoth;
                        }                        
                        margin:  20px 25px 20px 30px;
                        .btnBlock {     
                            float: left;                           
                            &:after {
                                @include clearBoth;
                            }
                            .updownBtn {
                                float: left;
                                background: none;
                                border: 1px solid #eee;
                                width: 30px;
                                height: 28px;
                            }
                            .goodsAmount {
                                float: left;
                                color: #333;
                                @include fontSize(12px,300);
                                width: 47px;
                                height: 28px;
                                border: 1px solid #eee;
                                @include flexAllCenter;
                                line-height: 14px;
                            }
                        }
                        .priceBlock {                 
                            float: right;         
                            @include flexCenter; 
                            &::after {
                                @include clearBoth;
                            }
                            .totalText {
                                @include fontSize(12px,500);
                                color: #333;
                                line-height: 18px;
                                margin-right: 5px;
                                float: left;
                            }
                            .totalNum {
                                @include fontSize(14px,500);
                                color: #333;
                                line-height: 20px;
                                float: left;
                                span {
                                    @include fontSize(18px,700);
                                    color: #333;
                                    line-height: 22px;
                                }
                            }
                        }
                    }
                    .btnBox {
                        .go {
                            width: 100%;
                            height: 60px;
                            background: $mainColor;
                            color: #ffffff;
                            @include fontSize(15px,500);
                            border: none;
                        }
                    }
                }
            }
        }
    }
}

