.fullPopup-wrapper {
	background: #fff;
	position: fixed;
	top: 0;
	right: -100%;
	width: 100%;
	height: 100vh;
	z-index: 11;
}

.fullPopup-wrapper.slideIn {
	right: 0;
	transition: .3s;
}

.fullPopup-wrapper.slideOut {
	right: -100%;
	transition: .3s;
}

.fullPopup-wrapper .header-section {
	height: 54px;
	padding-left: 15px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	position: relative;
}

.fullPopup-wrapper .header-section p {
	line-height: 29px;
	font-size: 20px;
	font-weight: 500;
}

.fullPopup-wrapper .header-section button {
	position: absolute;
	right: 10px;
	top: 50%;
	transform: translate(0, -50%);
	background: none;
	border: 0;
	width: 29px;
	height: 29px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
}

.fullPopup-wrapper .tab-section > ul {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	border-bottom: 1px solid #eee;
}

.fullPopup-wrapper .tab-section > ul > li {
	text-align: center;
	height: 42px;
	width: calc( 100%/3);
	display: -webkit-box;
