.content-body {  
    &.review-content {
        padding: 54px 0px 60px;
        .target-wrap{          
            .tabTarget {
                padding: 0 15px;
                .review-wrapper {
                    @include flexCenter;
                    padding: 20px 0;
                    border-bottom: 1px solid #eeeeee;    
                    &:last-child{
                        border-bottom: 0;            
                    }        
                    .img-box {
                        background: url(../img/image_09.png) center center;
                        width: 80px;
                        height: 80px;
                        margin-right: 10px;
                    }
                    .word-box {
                        height: 80px;
                        width: calc( 100% - 90px);
                        position: relative;   
                        &.cancle {
                            opacity: 0.5;
                        }
                        .toTop {      
                            margin-bottom: 10px; 
                            position: relative;                    
                            &:after {
                                @include clearBoth;
                            }
                            p {
                                @include fontSize(11px,300);
                                color: #333333;
                                float: left;
                                line-height: 14px;
                                margin-top: 2px;
                                span {
                                    color: #fff;
                                    @include fontSize(12px,300);
                                    line-height: 18px;
                                    background: #000;
                                    padding: 3px 9px;
                                    border-radius: 12px;
                                    margin-right: 5px;
                                }
                            }
                            button {
                                &.complete {
                                    border: 1px solid #ee5322;
                                    p {
                                        color: #ee5322;
                                    }
                                }
                                border: 1px solid rgba(#000, 0.2);
                                background: #fff;
                                position: absolute;
                                right: 0;
                                height: 28px;
                                width: 60px;
                                @include flexAllCenter;
                            }
                        }
                        .menuInfo {
                            p {
                                color: #333;
                                @include fontSize(14px,500);
                                line-height: 20px;
                                @include flexCenter;
                                @include wordClear;
                                width: 100%;
                            }
                        }
                        .price { 
                            position: absolute;
                            width: 100%;
                            bottom: -2px;
                            &:after {
                                @include clearBoth;
                            }                   
                            .priceNum {
                                float: right;
                                color: #333;
                                @include fontSize(12px,400);
                                span {
                                    color: #333;
                                    @include fontSize(15px,700);
                                    line-height: 18px;
                                }
                            }
                        }
                    }
                }
            }
        }
        .moreBtn-container {
            border-top: 1px solid #eee;
            button {
                border: 0;
                background: none;
                height: 40px; 
                width: 100%; 
                color: #000;
                @include fontSize(12px,400);
                line-height: 23px;
                img {
                    margin-left: 5px;
                    height: 8px;
                    width: 10px;
                    top: 50%;    
                }
            }
        }
    }
}