.content-body {
    &.pickupOrder {
        padding: 54px 0 0;
        .content-inner {
            height: 100vh;
            .textSection {
                @include fontSize(14px,700);
                line-height: 23px;
                padding: 10px 15px 0;
            }            
            .titleSection {
                border-bottom: 1px solid #eeeeee;    
                padding: 10px 15px 25px;
                &:after {
                    @include clearBoth;
                }
                &:last-child{
                    border-bottom: 0;            
                }        
                .img-area {
                    background: url(../img/image_09.png) center center;
                    width: 80px;
                    height: 80px;
                    margin-right: 10px;        
                    float: left;          

                }
                .word-area {
                    width: calc( 100% - 90px);
                    float: right;
                    .top-box {      
                        margin-bottom: 10px; 
                        width: 100%;         
                        p {
                            @include fontSize(11px,300);
                            color: #333333;
                            line-height: 14px;
                            margin-top: 2px;
                            span {
                                color: #fff;
                                @include fontSize(12px,300);
                                line-height: 18px;
                                background: #000;
                                padding: 3px 9px;
                                border-radius: 12px;
                                margin-right: 5px;
                            }
                        }
                    }
                    .menu-box {
                        margin-bottom: 11px;
                        p {
                            color: #333;
                            @include fontSize(14px,500);
                            line-height: 22px;
                            @include flexCenter;
                            width: calc( 100% - 5px);
                        }
                    }
                    .price-box { 
                        float: right;                        
                        &:after {
                            @include clearBoth;
                        }                   
                        .priceNum {
                            float: left;
                            color: #333;
                            @include fontSize(12px,400);
                            margin-right: 5px;
                            line-height: 18px;
                            span {
                                color: #333;
                                @include fontSize(15px,700);
                                line-height: 18px;
                            }
                        }
                        .pricePer {
                            float: right;
                            color: #333;
                            @include fontSize(12px,400);
                            line-height: 18px;
                            span {
                                color: #333;
                                @include fontSize(15px,700);
                                line-height: 18px;
                            }
                        }
                    }
                }
            }
            .barSection {
                height: 7px;
                width: calc( 100% + 2px );
                background: #f6f6f6;
                border: 1px solid #dcdcdc;
            }
            .mainSection {
                .content-area {
                    padding: 25px 15px;
                    &.lastMenu {
                        padding-bottom: 30px;
                    }
                    .info-box {
                        .detailItem {


                            .paragraphTitle {
                                @include fontSize(14px,700);
                                line-height: 20px;
                                color: #000;
                                margin-bottom: 10px;        
                            }
                            .tableTitle {        
                                @include fontSize(14px,500);
                                color: #333;
                                line-height: 20px;
                                width: 94px;
                                margin-bottom: 5px;
                            }
                            .goodsContent {

                                

                            }  
                        }
                    }
                }
            }
        }
    }
}